import React from "react"

import {heatmaps} from "../../resources/color.js"
import "../../styles/pages.css"
import '../input.css'
import {ToggleButtonGroup, ToggleButton} from 'react-bootstrap'

const {hslToString, getComplement} = require('../../resources/color.js')

function SettingsPage(props) {

    const handleClick = (heatMapName) => {
        let heatmap = heatmaps.filter(element => element.name === heatMapName)[0]
        props.setSettings(previousState => {
            return { ...previousState, 
                    "heatmap":heatmap,
            }
        })
    }

    const toggles = heatmaps.map(e => 
                <ToggleButton 
                    key={e.name} 
                    id={e.name} 
                    value={e.name} 
                    variant="outline-primary"  
                    onClick={(btn) => handleClick(e.name)}
                >
                    { e.name }
                </ToggleButton>
        )

    return  <div className="container">            
                <br />
                <label>Select the heatmap color: </label>{' '}
                <ToggleButtonGroup type="radio" name="options" defaultValue={props.settings.heatmap.name}>
                    {toggles}
                </ToggleButtonGroup>
                <br />
                <SampleTable heatmap={props.settings.heatmap} />
            </div>


}

function SampleTable(props) {

    let rows = []
    for (let i = 61; i > 0; i = i - 10) {
        let color = props.heatmap.func(i, 1, 61)
        let style = {
            background: hslToString(color),
            color: hslToString(getComplement(color)),
            width: "5em",
        }
        rows.push(<td key={`col:${i}`} style={style}>{props.heatmap.name}</td>)
    }

    let style = {
        marginTop:"1em"
    }

    return <table className='lines' style={style}>
        <tbody>
            <tr>
                {rows}
            </tr>
        </tbody>
    </table>
}
 
export default SettingsPage