
import React, { useState } from "react"

import EditableMatrix from "../EditableMatrix"

import {Button, ButtonGroup, ToggleButton, ToggleButtonGroup, OverlayTrigger, Popover} from 'react-bootstrap'
import Matrix from "../Matrix"
import { setDirectMatrix } from "../../resources/project"


let { deepCopy,  }  = require('../../resources/utils')

function EditMatrixPage(props) {

    //Used to react to changes when the matrix is edited
    const [isEdited, setEdited] = useState(false)
    const [matrix, setMatrix] = useState(props.project.editArrayObj)
    const [toggleStates, setToggleStates] = useState([false, false, false])
    const [isBatch, setBatch] = useState(false)

    const handleSave = () => {

        props.setProject(setDirectMatrix(matrix))
        setEdited(false)
    }

    const handleUndoRecent = () => {
        setEdited(false)

        setMatrix(props.project.direct.matrix)
        setToggleStates([false, false, false])
    }

    const handleRevert = () => {
        setEdited(false)

        setMatrix(props.project.originalMatrix)
        setToggleStates([false, false, false])
        handleSave()
    }

    const handleRemoveAll = (btn) => {
        let states = [...toggleStates]
        states[btn.value - 1] = btn.checked

        let removalResult = deepCopy(props.project.direct.matrix)
        for(let i = 0; i < removalResult.length; i++) {
            for(let j = 0; j < removalResult[i].length; j++) {
                removalResult[i][j] = (states[Math.abs(removalResult[i][j]) - 1]) ? 0 : removalResult[i][j]
            }
        }
        setEdited(true)
        setMatrix(removalResult)
        setToggleStates(states)
    }

    const toggles = [1, 2, 3].map((e, index) => 
        <ToggleButton
            type="checkbox"
            checked={toggleStates[index]} 
            key={e} 
            id={e} 
            value={e} 
            variant="outline-primary"
            onChange={event => handleRemoveAll(event.target)}  
        >
            { e }
        </ToggleButton>
    )

    const switchButtons = ["Individual Edit", "Batch Edit"].map(e => 
        <ToggleButton 
            key={e} 
            id={e} 
            value={e} 
            variant="outline-primary"  
            onClick={() => setBatch(e === "Batch Edit")}
        >
            { e }
        </ToggleButton>
    )

    const undoPopover = (
        <Popover id="popover-basic">
          <Popover.Header as="h3">Warning</Popover.Header>
          <Popover.Body>
            {/* TODO write actual explanation... */}
            Some weird shit going on here...Make sure you understand what these buttons do. TODO: Write actual explanation...
          </Popover.Body>
        </Popover>
    )

    const revertPopover = (
        <Popover id="popover-basic">
          <Popover.Header as="h3">Warning</Popover.Header>
          <Popover.Body>
              {/* TODO write actual explanation... */}
            Some weird shit going on here...Make sure you understand what these buttons do. TODO: Write actual explanation...
          </Popover.Body>
        </Popover>
    )
 

    return  <div className="container">
                <div className="centered">           
                    <Button variant="outline-primary" onClick={handleSave} disabled={!isEdited}>Save</Button>{' '}
                    <OverlayTrigger
                        placement="bottom"
                        
                        overlay={
                            undoPopover
                        }>
                        <Button variant="outline-primary" onClick={handleUndoRecent} disabled={!isEdited}>Revert to Last Save</Button>
                    </OverlayTrigger>
                    {' '}
                    <OverlayTrigger
                        placement="bottom"
                        overlay={
                            revertPopover
                        }>
                        <Button variant="outline-primary" onClick={handleRevert} disabled={!isEdited}>Revert to Original</Button>
                    </OverlayTrigger>
                </div> 
                <br />
                <div className="centered"> 
                    <ToggleButtonGroup type="radio" name="options" defaultValue={"Individual Edit"}>
                        {switchButtons}
                    </ToggleButtonGroup>
                </div>
                <br/> 
                { 
                    isBatch &&
                    <div>
                        Remove all:{' '} 
                        <ButtonGroup type="checkbox" name="options">
                            {toggles}
                        </ButtonGroup>
                        <br />
                        <br />
                    </div>
                }
                {   
                    isBatch ? 
                        <Matrix
                            project={props.project} 
                            array={matrix} 
                            factors={props.project.factorNames} 
                            heatmap={props.settings.heatmap}
                            />
                        :
                        <EditableMatrix 
                            array={matrix} 
                            factors={props.project.factorNames} 
                            heatmap={props.settings.heatmap.func}
                            setEdited={setEdited}
                            setMatrix={setMatrix}
                            /> 
                }
            </div>


}

export default EditMatrixPage