
import React from "react"

import Matrix from "../Matrix.js"

import { setIndirectMatrixByIterations } from "../../resources/project.js"

function IndirectPage(props) {

    const handleSelectMenu = (e) => {
        props.setProject(setIndirectMatrixByIterations(parseInt(e.target.value)))
    } 

    const options = []
    for(let i = 0; i <=6; i++) {
        options[i] = (<option value={i} key={"option-" + i}>{i}</option>)
    }

    return  <div>
                <div className="centered">           
                    <label>Number of Iterations: </label>
                    <select id='iterator' name='iterator' defaultValue={props.project.indirect.indirectN} onChange={(e) => handleSelectMenu(e)}>
                        {options}
                    </select>
                </div> 
                <br/> 
                <Matrix 
                    matrix={props.project.indirect}
                    factors={props.project.factorNames} 
                    heatmap={props.settings.heatmap}/>
            </div>


}

export default IndirectPage