import React from "react"
import Table from 'react-bootstrap/Table';


function InfluenceTable(props) {

    let influenceTds = Array.from(props.inf).map(([key, value]) => 
        <td className='lines' key={"td-inf-" + key}>{`${key} : ${value}`}</td>)

    let dependenceTds = Array.from(props.dep).map(([key, value]) => 
        <td className='lines' key={"td-dep-" + key}>{`${key} : ${value}`}</td>)

    let rows = influenceTds.map((td, index) => (<tr key={"tr-" + index}>{td}{dependenceTds[index]}</tr>))

    return <Table striped bordered hover>
            <thead>
                <tr key={"tr-headers"} >
                    <th key={"th-influence"} >Influence</th>
                    <th key={"th-dependence"} >Dependence</th>
                </tr>
            </thead>
            <tbody>
                {rows}
            </tbody>
        </Table>
    
}

export default InfluenceTable