const Colors = require('./color').Colors

export const NUM_LOOPS = 5

    /** 
     * A function which takes in an 2D array of NxN integers
     * and converts it to an array of objects which holds the integer
     * as well as the x,y coordinance in the array for JSX state.
     * 
     * @param {*} theArray 
     *                  an orderded 2D array of real numbers
     *                       ex: [[0, 3, -1],
     *                           [1, 0, -3],
     *                           [0, 3,  0]]
     *
     * @returns a 2D array of objects that contain both data and location
     * 
     *                       ex: [[{data:0,x:0,y:0}, {data:3,x:0,y:1}, {data:-1,x:0,y:2}],
     *                           [[{data:1,x:1,y:0}, {data:0,x:1,y:1}, {data:-3,x:1,y:2}],
     *                           [[{data:0,x:2,y:0}, {data:3,x:2,y:1}, {data:0,x:2,y:2}]
     */
export function Objectify(theArray) {
    let returnMe = []
    for(let i = 0; i < theArray.length; i++) {
        let innerArray = []
        for(let j = 0; j < theArray[0].length; j++) {
            innerArray.push({
                data: parseInt(theArray[i][j]),
                x: i,
                y: j,
            })
        }
        returnMe.push(innerArray)
    }
    return returnMe
}

export function deepCopy(the2DArray) {
    let result = []
    for(let i = 0; i < the2DArray.length; i++) {
        let innerArray = []
        for(let j = 0; j < the2DArray[0].length; j++) {
            innerArray[j] = the2DArray[i][j]
        }
        result.push(innerArray)
    }
    return result
}

export function buildUnweightedAdjacency(theWeightedArray) {
    let result = []
    for(let i = 0; i < theWeightedArray.length; i++) {
        let innerArray = []
        for(let j = 0; j < theWeightedArray[0].length; j++) {
            innerArray[j] = (theWeightedArray[i][j] > 0 ? 1 : theWeightedArray[i][j] < 0 ? -1 : 0)
        }
        result.push(innerArray)
    }
    return result
}

/**
 * A function which strips the 2D array of Objects
 * of their location and returns a 2D array of just
 * their data.
 * @param {*} theArray
 *                      a 2D array of objects that contain both data and location
*                           ex: [[{data:0,x:0,y:0}, {data:3,x:0,y:1}, {data:-1,x:0,y:2}],
*                           [[{data:1,x:1,y:0}, {data:0,x:1,y:1}, {data:-3,x:1,y:2}],
*                           [[{data:0,x:2,y:0}, {data:3,x:2,y:1}, {data:0,x:2,y:2}]
* 
* @returns  a 2D array of only the data
*                       ex: [[0, 3, -1],
*                           [1, 0, -3],
*                           [0, 3,  0]]
*/
export function getNums(theArray) {
    let returnMe = []
    for(let i = 0; i < theArray.length; i++) {
        let innerArray = []
        for(let j = 0; j < theArray[0].length; j++) {
            innerArray.push(parseInt(theArray[i][j].data))
        }
        returnMe.push(innerArray)
    }
    return returnMe
}



/**
 * A function which packages factors, influence map, and
 * dependence map into a single array of objects that include one
 * data point for Chart data visualization.
 * 
 * @param {*} theFactors 
 *                  an ordered 1D array of "factor names" representing Vertices the Graph. The order of 
 *                  the "factor names" in the array correspond with order of Vertices original adjacency matrix  
 *                      ex: ['A', 'B', 'C', 'D', 'E'] 
 * @param {*} theInf 
 *                  a map of all the factors paired with their normalized influence value
 * 
 * @param {*} theDep 
 *                  a map of all the factors paired with their normalized dependency value
 * 
 * @param {*} theColor
 *                  a color used for the graph to display each data point
 * 
 * @returns an array of objects formatted for Recharts graph with following key:value pairs:
 *                  [{
 *                  name: displays name of Factor on graph
 *                  inf: influence value used to graph on Y coordinates of graph
 *                  dep: dependence value used to graph on X coordinates of graph
 *                  color: color used to color plot point
 *                  }, ...]
 */

export function setupInfluenceMap(theFactors, theInf, theDep, theColor) {

    let returnMe = []
    for(let i = 0; i < theFactors.length; i++) {
        let inner =[]
        inner.push({
            name: theFactors[i],
            inf: theInf.get(theFactors[i]),
            dep: theDep.get(theFactors[i]),
            color: theColor
        })
        returnMe.push(inner)
    }
    return returnMe
}

/**
 * A function which packages factors, influence map, and
 * dependence map into a 2D array of objects that include two
 * data points for Chart data visualization.
 * 
 * @param {*} theFactors 
 *                  an ordered 1D array of "factor names" representing Vertices the Graph. The order of 
 *                  the "factor names" in the array correspond with order of Vertices original adjacency matrix  
*                      ex: ['A', 'B', 'C', 'D', 'E'] 
* @param {*} theInf 
*                  a map of all the factors paired with their normalized influence value
*                  of the direct data
* 
* @param {*} theDep 
*                  a map of all the factors paired with their normalized dependency value
*                  of the direct data
* 
* @param {*} theIteInf 
*                  a map of all the factors paired with their normalized influence value
*                  of the indirect data
* 
* @param {*} theIteDep 
*                  a map of all the factors paired with their normalized dependency value
*                  of the indirect data
* 
* 
* @returns an array of objects formatted for Recharts graph with following key:value pairs:
*                  [[{
*                  name: displays name of Factor on graph
*                  inf: influence value of direct data used to graph on Y coordinates of graph
*                  dep: dependence value of direct data used to graph on X coordinates of graph
*                  color: primary color used for first point
*                  },
*                  name: no name used for second point
*                  inf: influence value of indirect data used to graph on Y coordinates of graph
*                  dep: dependence value of indirect data used to graph on X coordinates of graph
*                  color: secondary color used for second point], ... ]
*/
export function setupTotalInfluenceMap(theFactors, theInf, theDep, theIteInf, theIteDep) {
    let returnMe = []
    for(let i = 0; i < theFactors.length; i++) {
        let inner =[]
        inner.push({
            name: theFactors[i],
            inf: theInf.get(theFactors[i]),
            dep: theDep.get(theFactors[i]),
            color: Colors.PRIMARY
        })
        inner.push({
            name: '',
            inf: theIteInf.get(theFactors[i]),
            dep: theIteDep.get(theFactors[i]),
            color: Colors.SECONDARY 
        })
        returnMe.push(inner)
    }
    return returnMe
}

/**
 * Function used to take in all the loops, factors, and matrix to build into a format that react-force-graph component
 * can use an argument and build properly.
 * 
 * @param {*} theLoops 
 *                  a list of an ordered 1D array of "factor names" representing Vertices in the loop. The order of 
 *                  the "factor names" in the array correspond with order of Vertices in the loop.  
 *                      ex: [['A', 'C', 'D'], ['A', 'C'], ...] 
 * 
 * @param {*} theFactors 
 *                  an ordered 1D array of "factor names" representing Vertices the Graph. The order of 
 *                  the "factor names" in the array correspond with order of Vertices original adjacency matrix  
 *                      ex: ['A', 'B', 'C', 'D', 'E'] 
 * 
 * @param {*} theArray 
 *                  an orderded 2D array of real numbers with each number representing a weighted edge
 *                  that corresponds to a vertice pointing to another vertice. 
 *                      ex: [[0, 3, -1],
 *                          [1, 0, -3],
 *                          [0, 3,  0]]
 * 
 * @returns an object with a list of all the nodes (Each vertice) and a list of all the links (each edge)
 *                      ex: {
 *                          nodes: ['A', 'B', 'C']
 *                          links: [{
 *                              source: 'A',
 *                              target: 'B',
 *                              value : 3
 *                              color : Colors.BLUE
 *                          }, ...]
 *                          }
 */

export function setupD3(theLoops, theFactors, theArray){
    let loops = theLoops.slice()
    loops.length = (loops.length > NUM_LOOPS) ? NUM_LOOPS : loops.length
    let nodes = []
    let Obnodes = []
    let links = []
    
    //Iterate through loops and add unseen vertice
    for(let i = 0; i < loops.length; i++) {
        loops[i].loop.forEach((element) => {
            if(!nodes.includes(element)){
                nodes.push(element)
            }
        })
    }

    //Converts nodes to object nodes to be used by force graph
    for(let i = 0; i < nodes.length; i++) {
        Obnodes.push({
            id: nodes[i],
            name: nodes[i],
            value: 1
        })
    }

    //Add links associated by nodes

    let max = 0
    let min = 0

    for(let i = 0; i <= loops.length-1; i++) {
        let loop = loops[i].loop
        for(let j = 0; j < loop.length; j++) {
            const next = (j + 1)%loop.length
            const start = loop[j]
            const end = loop[next]
            const value = theArray[theFactors.indexOf(start)][theFactors.indexOf(end)]
            const color = (value < 0) ? Colors.RED : Colors.BLUE
            if (Math.abs(value) > max) max = value
            if (Math.abs(value) < min) min = value
            if(value !== 0){
                links.push({
                    source: loop[j],
                    target: loop[next],
                    value: value,
                    color: color,
                    sign: value,
                })
            }
        }
    }

    //Normalize points
    links.forEach((element) => element.value = ((Math.abs(element.value)-min)/((max-min)*0.25)) + 1)
    return {
        nodes: Obnodes,
        links: links
    }
}

export function setupD3HighLights(theLoops, theIndex) {
    let loops = theLoops.slice()
    loops.length = (loops.length > NUM_LOOPS) ? NUM_LOOPS : loops.length
    let nodes = new Set()
    let links = new Set()

    let loop = loops[theIndex].loop
    for(let i = 0; i < loop.length; i++) {
        const next = (i + 1)%loop.length
        const start = loop[i]
        const end = loop[next]
        nodes.add(start)
        links.add(start+end)
    }

    return {
        nodes: nodes,
        links: links
    }
}

/**
 * Function used to estimate the total complexity of the feedback loop analysis
 * 
 * @param {*} theFactors
 *                  an ordered 1D array of "factor names" representing Vertices the Graph. The order of 
 *                  the "factor names" in the array correspond with order of Vertices original adjacency matrix  
 *                      ex: ['A', 'B', 'C', 'D', 'E'] 
 * 
 * @param {*} theLength 
 *                  the max length of the loops
 * 
 * @param {*} thePerc
 *                  the percentage of all the edges used
 * 
 * @returns the number of posible loops that can be found.  Used to estimate how long an analysis may take
 */
export function getComplexity(numFactors, theLength, thePerc){
    let length = theLength > numFactors ? numFactors : theLength
    let returnMe = 1
    for(let i = length-2; i >= 0; i--) {
        returnMe *= ((numFactors-i)*thePerc)
    }
    return parseInt(returnMe)
}

/**
 * Function used to calculate how many times a factor appears in all of the loops
 * 
 * @param {*} theLoops 
 *                      a list of an ordered 1D array of "factor names" representing Vertices in the loop. The order of 
 *                      the "factor names" in the array correspond with order of Vertices in the loop.  
 *                      ex: [['A', 'C', 'D'], ['A', 'C'], ...]  
 * @returns 
 */
export function getFrequency(theLoops){
    let freq = new Map()
    theLoops.forEach((element) => {
        element.loop.forEach((factor) => {
            if (freq.has(factor)) {
                freq.set(factor, freq.get(factor) + 1)
            } else {
                freq.set(factor, 1)
            }
        })
    })
    const returnMe = new Map([...freq.entries()].sort((a,b) => b[1] - a[1]))
    return returnMe
}

export function calculateConnectedPercentage(adjacency) {
    const vCount = adjacency.length
    const edgeCount = [].concat(...adjacency).filter(e => e !== 0).length

    return  edgeCount / ((vCount * vCount) - vCount)
}
