import React from 'react';
import {XAxis, YAxis, Scatter, ScatterChart, CartesianGrid, Tooltip, LabelList, Label, Cell, Legend} from 'recharts'

let Colors =  require('../resources/color').Colors

class Chart extends React.Component {
    render= () => {
        
        /** Container used to help render legend */
        let colors = [
            {
                name: 'Direct',
                color: Colors.PRIMARY
            },
            {
                name: 'Indirect',
                color: Colors.SECONDARY
            }
        ]

        //** Function used to render legend */
        const renderLegend = (props) => {
            return (
              <ul>
                {
                    colors.map((element) => 
                    <li key={`item-${element.name}`} style={{color: element.color,
                    width: '12.5%',
                    margin: 'auto',
                    fontSize: '25px'}}>
                        <span style={{fontSize: '18px'}}>{element.name}</span></li>
                    )}
              </ul>
            );
          }
        
        /** Packages 'Scatters' or points together to make individual points or pairs
         * Note only one Scatter will cause all the points to have lines connecting them preventing
         * differentiation to be impossible so one scatter plot per factor.  If there are multiple 
         * points in a scatter plot, it will be colored based on color it is assigned in props.
         * Note Documentation in 'utils.js' for setupTotalInfluenceMap for multiple points per factor
         */
        let scatters = []
        for (let i = 0; i < this.props.data.length; i++) {
        scatters.push(
            <Scatter data={this.props.data[i]} line={{stroke: 'black', strokeWidth: 2}}>
            {this.props.data[i].map( (entry, index) => {
            return <Cell fill={entry.color}/>
            })}
            <LabelList dataKey="name" position='bottom' fill = 'black' />
        </Scatter>)
        }
        return (
            <div className="Chart" >
                <ScatterChart width={1000} height={1000}
                  style = {{display: 'flex',
                    flexDirection:'column',
                    alignItems: 'center',
                    margin:'auto'}} >
                    <CartesianGrid/>
                    <Legend content={renderLegend}/>
                    <XAxis type="number" dataKey="dep" name="dependence" ticks={['0.5']}>
                        <Label value = "Dependence" offset={0} position="insideBottom"/>
                    </XAxis>
                    <YAxis type="number"
                        dataKey="inf"
                        name="influence"
                        label={{value: 'Influence', angle: -90, position: 'insideLeft'}}
                        ticks={['0.5']}
                      />
                    <Tooltip animationEasing='ease-out'/>
                    {scatters}
                </ScatterChart>
            </div>
        )
    }
}

export default Chart