export function loadProjectFromPmf(project) {
        //parsed file: {factors: [String], array: [num][num]}
    // factors: array of factor names
    // array: 2D "weighted" "directional" adjacency matrix

    const longNames = project.factorNames.map(nameObj => nameObj.longFactorName)

    const adjacency = buildUnweightedAdjacency(project.direct.matrix)



    const directInfluence = influence(project.direct.matrix, longNames)
    const directDependence = dependence(project.direct.matrix, longNames)
    const directEigenScores = calculateCentralityVectors(project.direct.matrix)

    const directRanks =  buildRankMap(project.factorNames, directInfluence, directDependence, directEigenScores)

    const indirect = iterate(project.direct.matrix, project.indirect.indirectN)

    const indirectInfluence = influence(indirect, longNames)
    const indirectDependence = dependence(indirect, longNames)
    const indirectEigenScores = calculateCentralityVectors(indirect)

    const indirectRanks =  buildRankMap(project.factorNames, indirectInfluence, indirectDependence, indirectEigenScores)

    return { 
        projectName: project.projectName,
        factorNames: project.factorNames, // [{longFactorName, shortFactorName}]
        editArrayObj: deepCopy(project.direct.matrix),
        originalMatrix: project.originalMatrix,
        // TODO package Graph info together adjacency % connected etc...
        graph: {
            unweightedAdjacency: adjacency,
            percentageConnected: calculateConnectedPercentage(adjacency),
        },
        direct:{
            matrix: project.direct.matrix, // [num][num] 2D "weighted" "directional" adjacency matrix
            influence: directInfluence,
            dependence: directDependence,
            eigenScores: directEigenScores,
            rankMap: directRanks,
        },
        indirect:{
            matrix: indirect, // [num][num] 2D "weighted" "directional" adjacency matrix
            influence: indirectInfluence,
            dependence: indirectDependence,
            eigenScores: indirectEigenScores,
            rankMap: indirectRanks,
            indirectN: project.indirect.indirectN,
        },
        feedbackLoops:{
            options:{
                loopLength: project.feedbackLoops.options.loopLength,
                factor: project.feedbackLoops.options.factor,
                weightAlgorithm: project.feedbackLoops.options.weightAlgorithm,
                loopCount: project.feedbackLoops.options.loopCount,
                isAnalyzing: false,
                loopWeight: project.feedbackLoops.options.loopWeight,
                currentLoop: project.feedbackLoops.options.currentLoop,
            },
            foundLoops: project.feedbackLoops.foundLoops,
        }
    }
}

// TODO: This function requires refactoring!!!
export function loadProjectFromCSV(fileText) {
    //parsed file: {factors: [String], array: [num][num]}
    // factors: array of factor names
    // array: 2D "weighted" "directional" adjacency matrix
    const parsedFile = JSON.parse(loadArray(fileText))

    //note: works for Latin and Greek based languages, not Chinese Japanese etc...
    const isAlpha = (char) => char.toLowerCase() !== char.toUpperCase()
    const isWhiteSpace = (char) => char === ' '

    const toFactorObj = (long) => {
        return {"longFactorName":long, "shortFactorName":toShortName(long)}
    }

    let toShortName = (longName) => {
        longName = longName.trim()
        if (longName.length < 3) {
            //edge case for very short "long" names
            return longName.toUpperCase()
        }

        let alphaWhiteSpaceOnly = Array.prototype.filter.call(longName, c => isWhiteSpace(c) || isAlpha(c))
                .join('')
                .split(' ')
                .filter(word => word !== '' && word.length > 3)
    
        if (alphaWhiteSpaceOnly.length === 1) {
            return alphaWhiteSpaceOnly[0].substring(0, 3).toUpperCase()
        } else  if (alphaWhiteSpaceOnly.length === 2) {
            return alphaWhiteSpaceOnly[0].substring(0, 2).toUpperCase() +
                    alphaWhiteSpaceOnly[1].substring(0, 1).toUpperCase()
        } else {
            return alphaWhiteSpaceOnly[0].substring(0, 1).toUpperCase() +
                    alphaWhiteSpaceOnly[1].substring(0, 1).toUpperCase() +
                    alphaWhiteSpaceOnly[2].substring(0, 1).toUpperCase()
        }
    }

    const genFrequencyTable = (factorNameObjects) => {
        let frequency = new Map()
    
        factorNameObjects.map(name => {
            frequency.has(name.shortFactorName) ?
                frequency.set(name.shortFactorName, frequency.get(name.shortFactorName) + 1) :
                frequency.set(name.shortFactorName, 1)
                return undefined
        })
        return frequency
    }
    

    //Ugly side-effecting function
    function adjustShortNames(factorNameObject) {
        let frequency = genFrequencyTable(factorNameObject)
        Array.from(frequency)
            .filter(([key, value]) => value > 1)
            .map(([name, count]) => name)
            .forEach(shortname => {
                let count = 1
                factorNameObject
                    .filter(nameObj => nameObj.shortFactorName === shortname)
                    .forEach(nameObj => nameObj.shortFactorName = nameObj.shortFactorName + count++)
            })
    }

    const factorNames = parsedFile.factors.map(e => e.substring(0, 3).toUpperCase())
                        .map((e, i) => {return toFactorObj(parsedFile.factors[i], e)}) 

    adjustShortNames(factorNames)

    const adjacency = buildUnweightedAdjacency(parsedFile.array)
    const longNames = factorNames.map(nameObj => nameObj.longFactorName)
    const directInfluence = influence(parsedFile.array, longNames)
    const directDependence = dependence(parsedFile.array, longNames)
    const directEigenScores = calculateCentralityVectors(parsedFile.array)

    const directRanks =  buildRankMap(factorNames, directInfluence, directDependence, directEigenScores)

    return { 
        projectName: "defualt",
        factorNames: factorNames, // [{longFactorName, shortFactorName}]
        editArrayObj: deepCopy(parsedFile.array),
        originalMatrix: deepCopy(parsedFile.array),
        // TODO package Graph info together adjacency % connected etc...
        graph: {
            unweightedAdjacency: adjacency,
            percentageConnected: calculateConnectedPercentage(adjacency),
        },
        direct:{
            matrix: parsedFile.array, // [num][num] 2D "weighted" "directional" adjacency matrix
            influence: directInfluence,
            dependence: directDependence,
            eigenScores: directEigenScores,
            rankMap: directRanks,
        },
        indirect:{
            matrix: parsedFile.array, // [num][num] 2D "weighted" "directional" adjacency matrix
            influence: directInfluence,
            dependence: directDependence,
            eigenScores: directEigenScores,
            rankMap: directRanks,
            indirectN: 0,
        },
        feedbackLoops:{
            options:{
                loopLength: 2,
                factor: factorNames[0].shortFactorName,
                weightAlgorithm: "Average Strength",
                loopCount: 10,
                isAnalyzing: false,
                loopWeight: "Direct Matrix",
                currentLoop: undefined,
            },
            foundLoops:[]
        }
    }
}

