export function setLoopsResult(loops) {
    return (previousState) => {

        previousState.feedbackLoops.foundLoops.push({
                    loops: loops,
                    loopLength: previousState.feedbackLoops.options.loopLength,
                    factor: previousState.feedbackLoops.options.factor,
                    weightAlgorithm: previousState.feedbackLoops.options.weightAlgorithm,
                    loopWeight: previousState.feedbackLoops.options.loopWeight,
                     
        })

        return {    ...previousState, 
                    feedbackLoops: {
                        options:{
                            loopLength: previousState.feedbackLoops.options.loopLength,
                            factor: previousState.feedbackLoops.options.factor,
                            weightAlgorithm: previousState.feedbackLoops.options.weightAlgorithm,
                            loopCount: previousState.feedbackLoops.options.loopCount,
                            isAnalyzing: false,
                            loopWeight: previousState.feedbackLoops.options.loopWeight,
                            currentLoop: previousState.feedbackLoops.foundLoops.length - 1,
                        },
                        foundLoops:previousState.feedbackLoops.foundLoops,
                    }
                }
            }
}


/*****************************************************************
 * Internal helper functions
 */

function buildRankMap(factorNames, influence, dependence, eigenScores) {
    let leftScores = factorNames.map((obj, index) => {
        return { name:obj.shortFactorName, score:eigenScores.pageRankCentrality[index]}
    }).sort((a, b) => b.score - a.score)
    let rightScores = factorNames.map((obj, index) => {
        return { name:obj.shortFactorName, score:eigenScores.eigenvectorCentrality[index]}
    }).sort((a, b) => b.score - a.score)
    const iMap = new Map(factorNames.map((obj, index) => {
        let influenceRank = Array.from(influence.keys()).indexOf(obj.longFactorName) + 1
        let dependenceRank = Array.from(dependence.keys()).indexOf(obj.longFactorName) + 1
        let rightEigenRank = rightScores.findIndex(element => element.name === obj.shortFactorName) + 1
        let leftEigenRank = leftScores.findIndex(element => element.name === obj.shortFactorName) + 1
        const info = {
            influence: influenceRank,
            dependence : dependenceRank,
            rightEigen: rightEigenRank,
            leftEigen: leftEigenRank,
        }

        return [obj.shortFactorName, info]
    }),)
    return iMap
}

