
import InfluenceTable from '../InfluenceTable'
import React, { useState,  } from "react"
import Chart from "../Chart.js"
import PFMS from "pfms"
import { Form}  from "react-bootstrap"

import "../../styles/pages.css"

//Use local modules
let Colors = require('../../resources/color').Colors
let Utils = require('../../resources/utils')

function InfluencePage(props) {

    const [chartOption, setChartOption] = useState(1)

    let data;

    let factors = props.project.factorNames.map((factor) => factor.longFactorName) 

    //// Builds displacement graph ////
    if(chartOption === 1) {
        data = Utils.setupInfluenceMap(factors, //Sets data for direct matrix
            PFMS.normalize(props.project.direct.influence), 
            PFMS.normalize(props.project.direct.dependence),
            Colors.PRIMARY)
    } else if (chartOption === 2) {
        data = Utils.setupInfluenceMap(factors, //Sets data for indirect matrix
            PFMS.normalize(props.project.indirect.influence),  
            PFMS.normalize(props.project.indirect.dependence),
            Colors.SECONDARY)
    } else if (chartOption === 3) {
        data = Utils.setupTotalInfluenceMap(factors, //Sets data for direct and indirect matrix 
            PFMS.normalize(props.project.direct.influence),
            PFMS.normalize(props.project.direct.dependence),
            PFMS.normalize(props.project.indirect.influence),
            PFMS.normalize(props.project.indirect.dependence))
    } else {
        console.log(" How TF am I here...? Empty!")
    }

    // TODO Display both direct and indirect -or- toggle

    return  <div className='container'>
                <table className="centered">
                    <thead>
                        <tr>
                            <td>
                                <InfluenceTable inf={props.project.direct.influence} dep={props.project.direct.dependence}/>
                            </td>
                            <td>
                                <InfluenceTable inf={props.project.indirect.influence} dep={props.project.indirect.dependence}/>
                            </td>
                        </tr>
                    </thead>
                </table>  
                <br />
                <Form.Select aria-label="Default select example" onChange={(opt) => {
                    setChartOption(parseInt(opt.target.value))
                }}>
                    <option value="1">Direct</option>
                    <option value="2">Indirect</option>
                    <option value="3">Both</option>
                </Form.Select> 
                <br />
                <Chart data={data} />
                
            </div>
}

export default InfluencePage