import React from "react"

import { OverlayTrigger, Popover } from 'react-bootstrap'

function FactorInfoOverlay(props) {

    return  <OverlayTrigger 
                placement={props.placement} 
                key={`ot-th-${props.placement}-${props.shortFactorName}`}
                overlay={
                <Popover id="popover-basic" key={`po-th-${props.placement}-${props.shortFactorName}`}>
                    <Popover.Header as="h3" className={`bg-${props.colorClass} text-white`} key={`po-hd-th-${props.placement}-${props.shortFactorName}`}>
                        <i className="bi bi-hammer" style={{paddingRight:"1em"}}/>{props.longFactorName}
                    </Popover.Header>
                    <Popover.Body className={`text-${props.colorClass}`} key={`po-bd-th-${props.placement}-${props.shortFactorName}`}>
                        <h3>{props.longFactorName} ({props.shortFactorName})</h3>
                        <hr />
                        <i className="bi bi-layout-text-sidebar" style={{paddingRight:".5em"}}/>Right Eigenvector: {props.info.rightEigen} out of {props.factorCount}
                        <br />
                        <i className="bi bi-layout-text-sidebar-reverse" style={{paddingRight:".5em"}}/>Left Eigenvector: {props.info.leftEigen} out of {props.factorCount}
                        <hr />
                        <i className="bi bi-bar-chart-steps" style={{paddingRight:".5em"}}/>Influence Rank: {props.info.influence} out of {props.factorCount}
                        <br />
                        <i className="bi bi-bar-chart-line-fill" style={{paddingRight:".5em"}}/>Dependence Rank: {props.info.dependence}  out of {props.factorCount}
                    </Popover.Body>
                </Popover> }>
                {props.element}
            </OverlayTrigger>
}

export default FactorInfoOverlay