import React from "react"
import { OverlayTrigger, Tooltip } from 'react-bootstrap';

function CentralityTable(props) {

    let eigRows = []
    eigRows.push(
        <tr key={"tr-top-row"}>
            <td key="td-factors-header" className='lines'>Factors</td>
            <OverlayTrigger
                placement="top"
                key={"ot-left-ev"}
                overlay={
                    <Tooltip key={"tt-left-ev"}>
                        = , where  is left eigenvector
                    </Tooltip>
                }>
                <td key={"td-left-ev"} className='lines'>Left Eigenvector</td>
            </OverlayTrigger>
            <OverlayTrigger
                placement="top"
                key={"ot-right-ev"}
                overlay={
                    <Tooltip key={"tt-right-ev"}>
                        =, where  is right eigenvector
                    </Tooltip>
                }>
                <td key={"td-right-ev"} className='lines'>Right Eigenvector</td>
            </OverlayTrigger>
        </tr>
    )

    for(let i = 0; i < props.factors.length; i++) {
        eigRows.push(
            <tr key={"tr-" + i}>
                    <OverlayTrigger
                        placement="left"
                        key={"ot-factor-name-" + props.factors[i].shortFactorName }
                        overlay={
                            <Tooltip key={"ot-factor-name-" + props.factors[i].shortFactorName }>
                                {props.factors[i].longFactorName}
                            </Tooltip>
                        }>
                        <td className='lines' key= {"td-factor-name-" + props.factors[i].shortFactorName}>
                            {props.factors[i].shortFactorName}
                        </td>
                    </OverlayTrigger>
                <td className='lines' key= {"td-factor-left-" + props.factors[i].shortFactorName}>{props.eigs.pageRankCentrality[i].toFixed(4)}</td>
                <td className='lines' key= {"td-factor-right-" + props.factors[i].shortFactorName}>{props.eigs.eigenvectorCentrality[i].toFixed(4)}</td>
            </tr>
        )
    }

    return <table className='lines'>
                <thead>
                    <tr>
                        <td  className='lines' colSpan='3'>Centrality Scores : Eigenvalue {props.eigs.eigenvalue.toFixed(4)}</td>
                    </tr>
                </thead>
                <tbody>
                    {eigRows}
                </tbody>
            </table>
    
}

export default CentralityTable