
import React,  {useState} from "react"
import PFMS from 'pfms'
import { OverlayTrigger, Collapse, Button, Container, Row, Col, Spinner, Popover, ToggleButtonGroup, ToggleButton } from 'react-bootstrap'
import Loop from "../Loop.js"

import 'bootstrap/dist/css/bootstrap.min.css'

  
import { getComplexity } from "../../resources/utils.js"
import { setMaxLoopLength, setMaxLoopCount, setLoopFactor, setLoopAlgorithm, setLoopsAnalyzing, setLoopsWeight, setCurrentLoop } from "../../resources/project.js"


/* Setting up Map used for rank preference - this needs to be pushed to the library...*/
const algorithmMap = new Map()
algorithmMap.set('Average Strength', PFMS.genClsAvgEdgeWeight)
algorithmMap.set('Total Strength', PFMS.genClsTotalEdgeWeight)
algorithmMap.set('Centrality via Edge Weight x Left Eigenvector', PFMS.genClsAvgEdgeWeightProdPageRank)
algorithmMap.set('Centrality via Edge Weight x Right Eigenvector', PFMS.genClsAvgEdgeWeightProdEigen)
algorithmMap.set('Centrality via Right Eigenvector (no Edge Weight)', PFMS.genClsAvgVertexEigen)


function FeedbackLoopsPage(props) {

    const LINK_STYLE =  {
        cursor: "pointer"
      }

    // const [open, setOpen] = useState(props.project.feedbackLoops.loops.both.length === 0)
    const [open, setOpen] = useState(true)
    const [loopSelection, setLoopSelection] = useState(2)

    const factorNameMap = new Map(
        props.project.factorNames.map(obj => {
            return [obj.shortFactorName, obj.longFactorName]
        }),)

    function handleLengthSelection (e) {
        props.setProject(setMaxLoopLength(e.target.value))
    }

    function handleWeightSelection (e) {
        props.setProject(setLoopsWeight(e.target.value))
    }

    function handleCountSelection (e) {
        props.setProject(setMaxLoopCount(e.target.value))
    }

    function handleFactorSelection (e) {
        props.setProject(setLoopFactor(e.target.value))
    }

    function handleAlgorithmSelection (e) {
        props.setProject(setLoopAlgorithm(e.target.value))
    }

    function setAnalyzing(isAnalyzing) {
        props.setProject(setLoopsAnalyzing(isAnalyzing))
    }

    function handleAnalyze() {
        setAnalyzing(true)
        const shortFactorNames = props.project.factorNames.map(e => e.shortFactorName)

        const payload = {
            adjacency: props.project.graph.unweightedAdjacency, 
            matrix: props.project.feedbackLoops.options.loopWeight === "Direct Matrix" ? props.project.direct.matrix : props.project.indirect.matrix,
            factorNames: shortFactorNames,
            factor:props.project.feedbackLoops.options.factor,
            maxLoopLength: props.project.feedbackLoops.options.loopLength,
            algorithmName: props.project.feedbackLoops.options.weightAlgorithm,
            maxLoopCount: props.project.feedbackLoops.options.loopCount
          }

          props.requestLoops(payload)
      }

    const lengths = []
    for (let i = 2; i <= props.project.factorNames.length; i++) {
        lengths.push(<option value={i} key={`op-loop-length-${i}`}>{i}</option>)
    }

    const factors = props.project.factorNames.map(element => 
        <option value = {element.shortFactorName} key={`opt-factor-${element.shortFactorName}`}>
            {element.shortFactorName}
        </option>
    )

    const algorithms = Array.from(algorithmMap).map(([key, value]) =>
        <option value = {key} key={`opt-algorithm-${key}`}>{key}</option>
    )

    const loopCounts = [5, 10, 25].map(count => 
        <option value = {count} key={`opt-loopCount-${count}`}>{count}</option>
    )

    const loopWeights = ["Direct Matrix", "Indirect Matrix"].map(matrix => 
        <option value = {matrix} key={`opt-loopWeights-${matrix}`}>{matrix}</option>
    )

    // const toggles = [
    //     <ToggleButton
    //         type="radio"
    //         key={"Reinforcing"} 
    //         id={"Reinforcing"} 
    //         value={1} 
    //         variant="outline-primary"
    //         onChange={event => {setLoopSelection(parseInt(event.target.value))}}>
    //             <>Reinforcing <i className="bi bi-arrow-counterclockwise"/></>
    //     </ToggleButton>,
    //     <ToggleButton
    //         type="radio"
    //         key={"Both"} 
    //         id={"Both"} 
    //         value={2} 
    //         variant="outline-primary"
    //         onChange={event => {setLoopSelection(parseInt(event.target.value))}} >
    //             <>Both</>
    //     </ToggleButton>,
    //     <ToggleButton
    //         type="radio"
    //         key={"Balancing"} 
    //         id={"Balancing"} 
    //         value={3} 
    //         variant="outline-primary"
    //         onChange={event => {setLoopSelection(parseInt(event.target.value))}}>
    //             <><i className="bi bi-arrow-clockwise"/> Balancing</>
    //     </ToggleButton>
    // ]

    
    function displayEstTimeComplexity() {
        const complexity = getComplexity(props.project.factorNames.length, props.project.feedbackLoops.options.loopLength, props.project.graph.percentageConnected)

        const className = complexity < 1000000 ? "bg-success text-white" : complexity < 10000000 ? "bg-warning text-dark" : "bg-danger text-white"
        const icon = complexity < 1000000 ? "bi bi-check-circle-fill" : complexity < 10000000 ? "bi bi-exclamation-triangle" : "bi bi-exclamation-octagon-fill"
        const spanWidth = complexity < 1000000000 ? "7em" : "10em"

        const STYLE =  {
            width: spanWidth, 
            display: "inline-block", 
            cursor: "pointer"
        }

        // console.log(`complexity: ${complexity} length: ${props.project.feedbackLoops.options.loopLength}`)

        return  <OverlayTrigger trigger="click" placement="bottom" overlay={
                    <Popover id="popover-basic">
                        <Popover.Header as="h3" className={className}><i className={icon} style={{paddingRight:"2em"}}/>    Time Complexity</Popover.Header>
                        <Popover.Body>
                            Wording about how long the user should expect this to take!
                            <br />
                            Here are some suggestions for lowering the time complexity:
                            <br />
                            * Shorter maximum loop length
                            <br />
                            * Remove some of the connectivity from the graph
                        </Popover.Body>
                    </Popover> }>
                    <span className={className} style={STYLE}>
                            {complexity.toLocaleString(undefined, { minimumFractionDigits:0})}
                        </span> 
                </OverlayTrigger>
    }

    let loopsArray = []
    let loopComps
    let toggles
    let description
    if(props.project.feedbackLoops.foundLoops.length > 0) {
        toggles = [
            <ToggleButton
                type="radio"
                key={"Reinforcing"} 
                id={"Reinforcing"} 
                value={1} 
                variant="outline-primary"
                onChange={event => {setLoopSelection(parseInt(event.target.value))}}>
                    <>Reinforcing <i className="bi bi-arrow-counterclockwise"/></>
            </ToggleButton>,
            <ToggleButton
                type="radio"
                key={"Both"} 
                id={"Both"} 
                value={2} 
                variant="outline-primary"
                onChange={event => {setLoopSelection(parseInt(event.target.value))}} >
                    <>Both</>
            </ToggleButton>,
            <ToggleButton
                type="radio"
                key={"Balancing"} 
                id={"Balancing"} 
                value={3} 
                variant="outline-primary"
                onChange={event => {setLoopSelection(parseInt(event.target.value))}}>
                    <><i className="bi bi-arrow-clockwise"/> Balancing</>
            </ToggleButton>
        ]
        if(loopSelection === 1) {
            loopsArray = props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].loops.reinforcing
        } else if (loopSelection === 2) {
            loopsArray = props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].loops.both
        } else if (loopSelection === 3) {
            loopsArray = props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].loops.balancing
        }
        let rankMap =  (props.project.feedbackLoops.options.loopWeight === "Direct Matrix") ?
            props.project.direct.rankMap :
            props.project.indirect.rankMap
        loopComps = loopsArray.map((loop, index) => 
            <Loop count={index} element={loop} rankMap={rankMap} nameMap={factorNameMap} key={`loop-${index}`}/>) 
        description =   <p>
                            {props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].factor}{' '}-{' '}
                            {props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].loopLength}{' '}-{' '}
                            {props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].weightAlgorithm}{' '}-{' '}
                            {props.project.feedbackLoops.foundLoops[props.project.feedbackLoops.options.currentLoop].loopWeight}
                        </p>
    }

    const infoLoopLength =  <OverlayTrigger trigger="click" placement="bottom" overlay={
                                <Popover id="popover-basic">
                                    <Popover.Header as="h3" className={"bg-info text-white"}><i className="bi bi-info-square-fill" style={{paddingRight:"2em"}}/>    Loop Length</Popover.Header>
                                    <Popover.Body className={"text-info"}>
                                        This setting determines the max number of factors a Loop may contain.  
                                        <br />
                                        For large and connected graphs, the number of loops grow exponentially.
                                        Limiting the max loop length may result in shorter calculation times. 
                                    </Popover.Body>
                                </Popover> }>
                                <span style={LINK_STYLE} className={"text-info"}>
                                    maximum<sup><i className="bi bi-info-square" /></sup>
                                </span>
                            </OverlayTrigger>

    const infoEstimate =    <OverlayTrigger trigger="click" placement="top" overlay={
                                <Popover id="popover-basic">
                                    <Popover.Header as="h3" className={"bg-info text-white"}><i className="bi bi-info-square-fill" style={{paddingRight:"2em"}}/>Estimate Information</Popover.Header>
                                    <Popover.Body className={"text-info"}>
                                        We do our best to estimate how long the calculation will take. CS algorithms speak...blah blah blah
                                        <br />
                                        More to come...
                                    </Popover.Body>
                                </Popover> }>
                                <span style={LINK_STYLE} className={"text-info"}>
                                    estimate<sup><i className="bi bi-info-square" /></sup>
                                </span>
                             </OverlayTrigger>

    return  <div className="container">
                { props.project.feedbackLoops.foundLoops.length > 0 &&
                    <Container>
                        <Button
                            onClick={() => setOpen(!open)}
                            variant="outline-primary"
                            size="sm"
                            aria-controls="example-collapse-text"
                            aria-expanded={open} >
                            Run another analysis{' '}{open ? <i className="bi bi-chevron-double-up" ></i> : <i className="bi bi-chevron-double-down" ></i>}
                        </Button>
                        <br />
                    </Container>
                }
                <Collapse in={open}>
                    <div>            
                        <label>Choose a factor to analyze:</label>{' '}
                        <select id='factor_to_analyze' name='factor_to_analyze' defaultValue={props.project.feedbackLoops.options.factor} onChange={(e) => handleFactorSelection(e)}>
                            {factors}
                        </select>
                        {' '}
                        <i className="bi bi-arrow-right-short" style={{paddingLeft:".5em", paddingRight:".5em"}}/>
                        {props.project.factorNames.filter(e => e.shortFactorName === props.project.feedbackLoops.options.factor).map(e => e.longFactorName)[0]}
                        <br />
                        <label> Loop {infoLoopLength} length:</label>{' '}
                        <select id='loop_length' name='loop_length' defaultValue={props.project.feedbackLoops.options.loopLength} onChange={(e) => handleLengthSelection(e)}>
                            {lengths}
                        </select>
                        <br/>
                        <label>Number of Loops to return:</label>{' '}
                        <select id='loop_count' name='loop_count' defaultValue={props.project.feedbackLoops.options.loopCount} onChange={(e) => handleCountSelection(e)}>
                            {loopCounts}
                        </select>
                        <br/>
                        <label>Algorithm Preference:</label>{' '}
                        <select id='algorithm' name='algorithm' defaultValue={props.project.feedbackLoops.options.weightAlgorithm} onChange={(e) => handleAlgorithmSelection(e)}>
                            {algorithms}
                        </select>
                        <br />
                        <label>Matrix weights: </label>{' '}
                        <select id='weights' name='weights' defaultValue={props.project.feedbackLoops.options.loopWeight} onChange={(e) => handleWeightSelection(e)}>
                            {loopWeights}
                        </select>
                        <br />
                        <label>The {infoEstimate} time complexity of analysis is: {displayEstTimeComplexity()}</label>{' '}
                        <br />
                        <Button 
                            id='feedbackbutton'
                            name='fbbutton' 
                            variant="outline-primary"
                            size="sm"
                            disabled={props.project.feedbackLoops.options.isAnalyzing} 
                            onClick={() => handleAnalyze()}>
                                Analyze{' '}
                                { props.project.feedbackLoops.options.isAnalyzing && <Spinner
                                    as="span"
                                    animation="border"
                                    size="sm"
                                    role="status"
                                    aria-hidden="true"
                                    />}
                        </Button>
                        <Container>
                            <Row>
                                <Col sm={2}></Col>
                                <Col sm={4}>
                                    <div className="d-grid gap-2">
                                        
                                    </div>
                                </Col>
                                <Col sm={2}></Col>
                            </Row>
                        </Container>
                    </div>
                </Collapse>
                <br />

                {props.project.feedbackLoops.foundLoops.length > 0 &&
                    <div>
                        <Button
                            style={{marginRight:"1em"}}
                            key={"pre"} 
                            id={"prev"} 
                            variant="outline-primary"
                            disabled={props.project.feedbackLoops.options.currentLoop === 0}
                            onClick={event => {props.setProject(setCurrentLoop(props.project.feedbackLoops.options.currentLoop - 1))}}>
                                <><i className="bi bi-chevron-double-left"/> Previous</>
                        </Button>
                        <ToggleButtonGroup type="radio" name="options" defaultValue={2}>
                            {toggles}
                        </ToggleButtonGroup>
                        <Button
                            style={{marginLeft:"1em"}}
                            key={"nxt"} 
                            id={"next"} 
                            variant="outline-primary"
                            disabled={props.project.feedbackLoops.options.currentLoop === props.project.feedbackLoops.foundLoops.length - 1}
                            onClick={event => {props.setProject(setCurrentLoop(props.project.feedbackLoops.options.currentLoop + 1))}}>
                                <>Next <i className="bi bi-chevron-double-right"/></>
                        </Button>   
                        <br />
                    {description}
                    {loopComps}
                    </div>
                }


            </div>
}

export default FeedbackLoopsPage