import React, { useState, useEffect, useRef } from "react"
import axios from 'axios'
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap"
import { DropdownSubmenu, NavDropdownMenu} from "react-bootstrap-submenu";

import InputFilePage from './pages/InputFilePage.js'
import IndirectPage from "./pages/IndirectPage.js"
import DirectPage from "./pages/DirectPage.js"
import CentralityPage from "./pages/CentralityPage.js"
import UnderConstructionPage from "./pages/ConstructionPage.js"
import HomePage from "./pages/HomePage.js"
import InfluencePage from "./pages/InfluencePage.js"
import SettingsPage from "./pages/SettingsPage.js"
import AllPage from "./pages/AllPage.js"
import EditMatrixPage from "./pages/EditMatrixPage.js"
import FeedbackLoopsPage from "./pages/FeedbacksLoopPage.js"

import {heatmaps} from "../resources/color.js"
import { EMPTY_PROJECT, setLoopsResult } from "../resources/project.js"
import { loadProjectFromCSV, loadProjectFromPmf, minifyProject } from '../resources/project'


/*
 CSS Imports
*/
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-icons/font/bootstrap-icons.css'
import 'react-bootstrap-submenu/dist/index.css'


function App() {

    const doFileDownload = useRef(null)
    const doFileUpload = useRef(null)

    //The initial empty project. All fields loaded in Input.js component. 
    let initialProject = EMPTY_PROJECT

    useEffect(() => {
        const wakeUpHeroku = async () => {
            await axios.get(
              `https://cfb3-tcss450-labs-helloworld.herokuapp.com/hello`,
            ).then(result => {
                //we literally don't care
            }).catch(err => {
                //TODO Add error handling
                console.log(err)
            })
          }
          wakeUpHeroku()
    }, [])

    //Used to store the "project"
    const [project, setProject] = useState(initialProject)
    const [isLoaded, setLoaded] = useState(false)
    const [settings, setSettings] = useState({heatmap:heatmaps[0],})
    const [fileDownloadUrl, setFileDownloadUrl] = useState("")

    //Used to store the current navigation location
    // const [nav, setNav] = useState("home")
    const [nav, setNav] = useState("home")

    useEffect(() => {
        if(fileDownloadUrl !== "") {
            console.log("download the file: " + fileDownloadUrl)
            doFileDownload.current.click()
            URL.revokeObjectURL(fileDownloadUrl)
            setFileDownloadUrl("")
        }
    }, [fileDownloadUrl])

    
    function requestLoops(payload) {
        // console.log(payload)
        const fetchData = async () => {
            await axios.post(
              //'http://localhost:5000/calculateLoops',  
             `https://cfb3-tcss450-labs-helloworld.herokuapp.com/calculateLoops`,
              payload
            ).then(result => {
                setProject(setLoopsResult(result.data.loops))
            }).catch(err => {
                //TODO Add error handling
                console.log(err)
            }) 
          }
          fetchData()
    }

   
    function onSaveClick() {
        let output = JSON.stringify(minifyProject(project), 
            null, 4);
        const blob = new Blob([output]);
        setFileDownloadUrl(URL.createObjectURL(blob))
    }

    function onLoadClick() {
        doFileUpload.current.click()
    }

    let showFile = async (e) => {
        e.preventDefault()
        const reader = new FileReader()
        reader.onload = async (e) => {
            let name = document.getElementById('fileInput').files.item(0).name.split('.')
            let extension = name[name.length - 1]
            if(extension === "csv") {
                setProject(loadProjectFromCSV(e.target.result))
                setLoaded(true)
                setNav("direct")
            } else if(extension === "pmf") {
                setProject(loadProjectFromPmf(JSON.parse(e.target.result)))
                setLoaded(true)
                setNav("direct")
            }
        }
        reader.readAsText(e.target.files[0])
        
    }

    function renderOnNavSelected(location) {
        switch(location) {
            case "home":
                return <HomePage matrix={project} setProject={setProject} />
            case "loadfile":
                return <InputFilePage matrix={project} setProject={setProject} setNav={() => setNav("direct")} setLoaded={setLoaded} />
            case "direct":
                return <DirectPage project={project} setProject={setProject} settings={settings} /> 
            case "indirect":
                return <IndirectPage project={project} setProject={setProject} settings={settings} />    
            case "edit":
                return <EditMatrixPage project={project} setProject={setProject} settings={settings} />    
            case "eigen":    
                return <CentralityPage project={project} setProject={setProject} settings={settings}/> 
            case "influence":
                return <InfluencePage project={project} setProject={setProject} settings={settings} />
            case "all":
                return <AllPage project={project} setProject={setProject} settings={settings} />
            case "fbloops":
                return <FeedbackLoopsPage project={project} setProject={setProject} settings={settings} requestLoops={requestLoops}/>   
            case "cld":
                return <UnderConstructionPage pagename="Causal Loop Diagram"/>   
            case "other":
                return <UnderConstructionPage pagename="Other Visualizations"/>   
            case "settings":
                return <SettingsPage project={project} setProject={setProject} settings={settings} setSettings={setSettings}/>
            case "aboutUs":
                return <UnderConstructionPage pagename="We made this"/> 
            case "pfmTheory":
                return <UnderConstructionPage pagename="Civil Engineering Speak"/> 
            case "graphTheory":
                return <UnderConstructionPage pagename="Computer Science Speak"/> 
            case "glossary":
                return <UnderConstructionPage pagename="Define these terms!?!?"/> 

            default:
                return  
            }
    }

    return (
        <>
            <a className="hidden" 
                //hidden anchor tag used to download the project
                style={{display:"none",}}
                download={project.projectName + ".pmf"}
                href={fileDownloadUrl}
                ref={doFileDownload}>
                    download it
            </a>
            <input ref={doFileUpload} 
                //hidden input to load files from 
                style={{display:"none",}} 
                type="file" 
                id="fileInput" 
                onChange = {(e) => showFile(e)} 
                accept='.csv, .pmf'
                />
            <Navbar collapseOnSelect bg="light" expand="xl" sticky="top" style={{paddingLeft:"1em"}} >
                <Navbar.Brand><i className="bi bi-mortarboard" style={{paddingRight:".5em"}}/>UWT-SET PFMS</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto"
                    justify variant="tabs" 
                    activeKey={nav}
                    onSelect={(selectedKey) => setNav(selectedKey)}>
                    <NavDropdownMenu title="Project" id="collapsible-nav-dropdown">
                        <DropdownSubmenu title="Load" id="collapsible-nav-dropdown-sub-load">
                            <NavDropdown.Item onClick={onLoadClick} >From File</NavDropdown.Item>
                            <NavDropdown.Item eventKey="loadserver" disabled="false">From Server</NavDropdown.Item>
                        </DropdownSubmenu>
                        <DropdownSubmenu title="Save" id="collapsible-nav-dropdown-sub-save">
                            <NavDropdown.Item onClick={onSaveClick} >To File</NavDropdown.Item>
                            <NavDropdown.Item eventKey="saveserver" disabled="false">To Server</NavDropdown.Item>
                        </DropdownSubmenu>
                        <NavDropdown.Item eventKey="create" disabled="false">Create</NavDropdown.Item>
                    </NavDropdownMenu>
                    <NavDropdown title="Matrix"  disabled={!isLoaded} id="basic-nav-dropdown">
                        <NavDropdown.Item eventKey="direct" disabled={!isLoaded}>Direct</NavDropdown.Item>
                        <NavDropdown.Item eventKey="indirect" disabled={!isLoaded}>Indirect</NavDropdown.Item>
                        <NavDropdown.Item eventKey="edit" disabled={!isLoaded}>Edit</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="Information" disabled={!isLoaded} id="basic-nav-dropdown">
                        <NavDropdown.Item eventKey="eigen" disabled={!isLoaded}>Eigen</NavDropdown.Item>
                        <NavDropdown.Item eventKey="influence" disabled={!isLoaded}>Influence</NavDropdown.Item>
                        <NavDropdown.Item eventKey="all" disabled={!isLoaded}>All</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="Analyze" disabled={!isLoaded} id="basic-nav-dropdown">
                        <NavDropdown.Item eventKey="fbloops" disabled={!isLoaded}>Feedback Loops</NavDropdown.Item>
                        <NavDropdown.Item eventKey="cld" disabled={!isLoaded}>Causal Loop Diagram</NavDropdown.Item>
                        <NavDropdown.Item eventKey="other" disabled={!isLoaded}>Other Visualizations</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="About" id="basic-nav-dropdown">
                        <NavDropdown.Item eventKey="aboutUs" >About Us</NavDropdown.Item>
                        <NavDropdown.Item eventKey="pfmTheory" >About PFM</NavDropdown.Item>
                        <NavDropdown.Item eventKey="graphTheory" >Graph Theory</NavDropdown.Item>
                        <NavDropdown.Item eventKey="glossary" >Glossary</NavDropdown.Item>
                    </NavDropdown>
                    <Nav.Link eventKey="settings"><i className="bi bi-gear-fill " ></i></Nav.Link>
                    
                </Nav>
                </Navbar.Collapse>
            </Navbar>
            <div>{ renderOnNavSelected(nav) }</div>
        </>
    )
    
}

  
export default App