import React from "react"

import { OverlayTrigger, Popover } from 'react-bootstrap'

import FactorInfoOverlay from "./FactorInfoOverlay"

import './input.css'

const {calculateGradient, findMaxMin, hslToString, getComplement} = require('../resources/color.js')

function Cell(props)  {

    let bg = props.data >= 0 ? hslToString(props.color) : calculateGradient(props.color)

    const STYLE = {
        background: bg,
        color: hslToString(getComplement(props.color))
    }

    // const NEGATIVE_STYLE = {
    //     background: bg,
    //     color: hslToString(getComplement(props.color)),
    //     border: "red solid 2px"
    // }

    const DIAG_STYLE = {
        background: "black",
        color: "white"
    }


    return props.isDiag ?
                <td style={DIAG_STYLE}
                    className='lines'
                    key={"td:" + props.start.shortFactorName + ' ' + props.end.shortFactorName}>
                        {Math.abs(props.data)}
                </td> 
            :
                <OverlayTrigger
                    placement={props.isTop ? "bottom" : "top"}
                    key={`ot-cell: ${props.start.shortFactorName} + ${props.end.shortFactorName}`}
                    overlay={
                        <Popover id="popover-basic">
                            <Popover.Header style={ STYLE} as="h3">{`${props.start.shortFactorName}  ${props.end.shortFactorName}`}</Popover.Header>
                            <Popover.Body>
                                {`${props.start.shortFactorName}: ${props.start.longFactorName}`}
                                    <br />
                                {`${props.end.shortFactorName}: ${props.end.longFactorName}`}
                                    <br />
                                <span style={{fontWeight: 'bold'}}>{`weight: ${props.data}`}</span>
                            </Popover.Body>
                        </Popover>
                    }>
                    <td style={ STYLE}
                                className='lines'
                                key={"td:" + props.start.shortFactorName + ' ' + props.end.shortFactorName}>
                                    {Math.abs(props.data)}
                            </td> 
                </OverlayTrigger>
}

function Matrix (props) {
    
    const factorHeaders = props.factors.map(data => {
        const COL_HEAD_STYLE = {
            paddingLeft: "1em",
            paddingRight: "1em",
            border:"1px solid black",
        }
        const element =  <th style={COL_HEAD_STYLE} key={`th-bottom-${data.shortFactorName}`}>
                            {data.shortFactorName}
                        </th>

        return  <FactorInfoOverlay 
                    placement="bottom"
                    colorClass={props.heatmap.colorClass}
                    shortFactorName={data.shortFactorName} 
                    longFactorName={data.longFactorName}
                    info={props.matrix.rankMap.get(data.shortFactorName)} 
                    factorCount={props.factors.length} 
                    element={element} />
    })

    // Below is all of the HTML/JSX used to actually draw out the component
    const td = props.factors.length === 0 ? <></> : <td key="empty table cell"/>
    const rows = []
    let columns;
    if (props.matrix.matrix.length !== 0) {
        const maxmin = findMaxMin([].concat(...props.matrix.matrix), (e) => Math.abs(e))
        columns =                    
            <tr key={"tr-top-row"}>
                {td}
                { factorHeaders }
            </tr>
        for(let i = 0; i <props.factors.length; i++) {
            const COL_HEAD_STYLE = {
                paddingLeft: "1em",
                paddingRight: "1em",
                border:"1px solid black",
            }
            const element =  <th style={COL_HEAD_STYLE} key={`th-right-${props.factors[i].shortFactorName}`}>
                                {props.factors[i].shortFactorName}
                            </th>
            rows.push(
                <tr key={"tr-row:" + i}>
                    <FactorInfoOverlay 
                        placement="right"
                        colorClass={props.heatmap.colorClass}
                        shortFactorName={props.factors[i].shortFactorName} 
                        longFactorName={props.factors[i].longFactorName} 
                        info={props.matrix.rankMap.get(props.factors[i].shortFactorName)} 
                        factorCount={props.factors.length}
                        element={element} />
                    {props.matrix.matrix[i].map((element, j) => (
                                   
                        <Cell
                            data={element}
                            color={props.heatmap.func(Math.abs(element), maxmin.min, maxmin.max)} 
                            isDiag={i === j}
                            start = {props.factors[i]}
                            end = {props.factors[j]}
                            isTop = {i < 3}
                            key={`cell: ${i} + ${j}`} />

                                        
                ))}
            </tr>
            )
        }
        return (
            <div>
                <table className='lines'>
                    <thead>
                        {columns}
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
            </div>
        )
    } else {
        return <h1>No data!</h1>
    }
}



export default Matrix