import React from "react"
import FactorInfoOverlay from "./FactorInfoOverlay"

function Loop(props) {
    
  const ICON_STYLE = {
    fontSize: "x-large", 
    fontWeight:"bold",
  }

    let sign = props.element.signum > 0 ?
                <i className="bi bi-arrow-counterclockwise" style={ICON_STYLE}/> :
                <i className="bi bi-arrow-clockwise" style={ICON_STYLE}/>

    let vertices = props.element.loop.map((vertex, index) => {
      const element = <span>
                        <Node name={vertex} key={`nd-${props.count}-${vertex}`}/>
                      </span>
      return  <span>

              <FactorInfoOverlay 
                              placement="bottom"
                              colorClass="primary"
                              shortFactorName={vertex} 
                              longFactorName={props.nameMap.get(vertex)}
                              info={props.rankMap.get(vertex)} 
                              factorCount={props.nameMap.length} 
                              element={element} />
              { 
                index < props.element.loop.length - 1 ?
                  <i className="bi bi-arrow-right-short" style={{fontSize: "x-large",}}/> : 
                  <i className="bi bi-box-arrow-in-right" style={{fontSize: "x-large",}}/>
              }
      </span> 
})

    const myStyle = {
        borderRadius: "25px",
        padding: "10px",
        fontFamily: "Sans-Serif",
        margin: "5px"
      }

    return <div>
                <label>
                    <span style={myStyle}>{`Loop ${props.count + 1} - `}{sign}{` - ${props.element.strength.toFixed(4)}`}</span>
                    <br />
                    <br />
                    {vertices}
                </label>
                <hr />
          </div>;
  }

  function Node(props) {
    const myStyle = {
        borderRadius: "25px",
        color: "white",
        backgroundColor: "DodgerBlue",
        padding: "10px",
        fontFamily: "Sans-Serif",
        margin: "5px"
      }
    return <span style={myStyle}>{props.name}</span>
  }
  
  export default Loop;