
import Matrix from "../Matrix.js"
import InfluenceTable from "../InfluenceTable.js"
import CentralityTable from "../CentralityTable.js"
import React from "react"

function AllPage(props) {


    return  <div>
                <InfluenceTable inf={props.project.direct.influence} dep={props.project.direct.dependence}/>
                <CentralityTable factors={props.project.factorNames} eigs={props.project.direct.eigenScores}/>            
                <Matrix 
                    project={props.project}
                    array={props.project.direct.matrix} 
                    factors={props.project.factorNames} 
                    heatmap={props.settings.heatmap}/>
                <Matrix 
                    project={props.project}
                    array={props.project.indirect.matrix} 
                    factors={props.project.factorNames} 
                    heatmap={props.settings.heatmap}/>
            </div>


}

export default AllPage