import React from "react"

import { OverlayTrigger, Tooltip } from 'react-bootstrap';

import './input.css'

const {calculateGradient, findMaxMin, hslToString, getComplement} = require('../resources/color.js')

function Cell(props)  {

    let bg = props.data >= 0 ? hslToString(props.color) : calculateGradient(props.color)

    const STYLE = {
        background: bg,
        color: hslToString(getComplement(props.color))
    }

    const DIAG_STYLE = {
        background: "black",
        color: "white"
    }



    if (props.x === props.y) {
        return  <td style={ DIAG_STYLE }
                    className='lines'
                    key={"td-" + props.x + ' ' + props.y}>
                        {Math.abs(props.data)}
                </td> 
    }

    const options = []
    for(let i = -3; i <= 3; i++) {
        options[i + 3] = (<option value={i} key={"td-"+ props.x + ' ' + props.y + "-opt-" + i}>{i}</option>)
    }
    
    return <td className='lines'
                key={"td-" + props.x + ' ' + props.y}
                style={STYLE} >
                <select id='iterator' name='iterator' defaultValue={props.data} onChange={props.onChange}>
                    { options }
                </select>
            </td>
}

function EditableMatrix (props) {

    const COL_HEAD_STYLE = {
        paddingLeft: "1em",
        paddingRight: "1em",
        borderLeft:"1px solid grey",
    }

    const ROW_HEAD_STYLE = {
        paddingLeft: "0.5em",
        paddingRight: "0.5em",
        borderTop:"1px solid grey",
    }

    /**
     * Event handler passed from parent component to child component 
     * 
     * @param {*} e 
     *                  the event
     * 
     * @param {*} i 
     *                  the row location in the 2D array that is being changed
     * 
     * @param {*} j 
     *                  the column location in the 2D array that is being changed
     * 
     * @returns void
     */
    function handleChange(e,i,j) {
        const newData = e.target.value
        if ((isNaN(newData)) || (newData === undefined)) return;
        let newArray = []
        for(let i = 0; i < props.array.length; i++) {
            newArray[i] = props.array[i].slice(0);
        }
        newArray[i][j] = parseInt(newData)
        props.setMatrix(newArray)
        props.setEdited(true)
        
    }

    const factorHeaders = props.factors.map(data => (
        <OverlayTrigger
            placement="top"
            key={"ot-" + data.shortFactorName}
            overlay={
                <Tooltip key={"tt-" + data.shortFactorName}>
                    {data.longFactorName}
                </Tooltip>
            }>
            <th style={COL_HEAD_STYLE} key= {"th-" + data.shortFactorName}>
                {data.shortFactorName}
            </th>
        </OverlayTrigger>
    ))

    // Below is all of the HTML/JSX used to actually draw out the component
    const td = props.factors.length === 0 ? <></> : <td/>
    const rows = []
    let columns;
    if (props.array.length !== 0) {
        const maxmin = findMaxMin([].concat(...props.array), (e) => Math.abs(e))
        columns =                    
        <tr key={"tr-top-row"}>
            {td}
            { factorHeaders }
        </tr>
        for(let i = 0; i <props.factors.length; i++) {
            rows.push(
                <tr key={"tr-row:" + i}>
                    <OverlayTrigger
                        placement="left"
                        key={"ot-row:" + props.factors[i].shortFactorName}
                        overlay={
                            <Tooltip key={"tt-row:" + props.factors[i].shortFactorName}>
                                {props.factors[i].longFactorName}
                            </Tooltip>
                        }>
                        <th style={ROW_HEAD_STYLE} key= {"th-row:" +props.factors[i].shortFactorName}>
                            {props.factors[i].shortFactorName}
                        </th>
                    </OverlayTrigger>
                    {   
                        props.array[i].map((element, j) => (
                                <Cell
                                    data={element}
                                    color={props.heatmap(Math.abs(element), maxmin.min, maxmin.max)}
                                    x = {i}
                                    y = {j}
                                    key={"Cell-" + i + ":" + j}
                                    onChange = { (e) => handleChange(e, i, j)}
                                />
                         ))
                    }
            </tr>
            )
        }
        return (
            <div>
                <table className='lines'>
                    <thead>
                        {columns}
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
            </div>
        )
    } else {
        return <h1>No data!</h1>
    }
}

export default EditableMatrix