//Importing npm packages and react components
import React from 'react'
import '../input.css'
import { loadProjectFromCSV } from '../../resources/project'

function InputFilePage (props) {

    /**
     * Function to load the file
     * 
     * @param {} e the event
     * 
     */
    let showFile = async (e) => {
        // clearMatrix()
        console.log(e)
        e.preventDefault()
        const reader = new FileReader()
        reader.onload = async (e) => {

        let name = document.getElementById('fileInput2').files.item(0).name.split('.')
        let extension = name[name.length - 1]

        if(extension === "csv") {
            props.setProject(loadProjectFromCSV(e.target.result))
            props.setLoaded(true)
            props.setNav() //callback telling the parent to set the navigation as needed
        }
    }

    reader.readAsText(e.target.files[0])
}

    // Below is all of the HTML/JSX used to actually draw out the component
    return (
    <div className='container'>
        <h1>Enter a file</h1>
        <input type="file" id="fileInput2" onChange = {(e) => showFile(e)} accept='.csv, .pmf'/>
    </div>
)}

export default InputFilePage
