const tc = require("tinycolor2")

/* Arrow function for setting up enumerations. */
const enumValue = (name) => Object.freeze({toString: () => name})

/* Setup Color enumerations to be used in other components. */
export const Colors = Object.freeze({
    PRIMARY: enumValue('#4b2e83'),  //UW purple
    SECONDARY: enumValue('#b7a57a'),  //UW Gold
    LIGHT: enumValue('white'),  //Generic White
    DARK: enumValue('black'),   //Generic Black
    RED: enumValue('red'),      //Generic Red
    BLUE: enumValue('blue')     //Generic Blue
})

/**
 * Function used to find the min and max value of a 2D array
 * 
 * @param {*} array
 *                  an unordered 2D array of real numbers 
 *                       ex: [[0, 3, -1],
 *                           [1, 0, -3],
 *                           [0, 3,  0]]
 * @param {*} getData
 *                  a function which decides how to sort the 2D array
 * @returns an object with the following key:value pairs:
 *                  min: a number representing the minimum value of the 2D array
 *                  max: a number representing the maximum value of the 2D array
 */
export function findMaxMin(array, getData) {
    let temp = array.map(e => getData(e))
    .filter(element => element !== 0) 
    let returnMe = {
        min: Math.min.apply(null, temp),
        max: Math.max.apply(null, temp)
    }
    return returnMe
}

/**
 * Function used to determine what color to give to a value for a heatmap
 * 
 * @param {*} value
 *                  any real number used to evaluate what color to assign it for a heat map
 * 
 * @param {*} maxmin
 *                  an object with the following key:value pairs:
 *                      min: a number representing the minimum value of the 2D array
 *                      max: a number representing the maximum value of the 2D array
 * 
 * @returns an object with the following key:value pairs:
 *                      red: a number between 0 - 255 representing the amount of red in a color
 *                      blue: a number between 0 - 255 representing the amount of blue in a color
 *                      green: a number between 0 - 255 representing the amount of green in a color
 */
export function calculateColor (value, maxmin) {
    if(value === 0) return {
        red: 0,
        green: 0,
        blue: 0,
    }
    let offset = maxmin.min * -1
    let difference = (maxmin.max - maxmin.min)
    let percent = ((value + offset) % (difference+1)) / (difference + 1);
    let r = 0, g = 0, b = 0;
    if(0 <= percent && percent <= 0.125) {
        b = 4 * percent + 0.5;
    } else if(0.125 <= percent && percent <= 0.375) {
        g = 4 * percent - 0.5;
        b = 1;
    } else if(0.375 <= percent && percent <= 0.625) {
        r = 4 * percent - 1.5;
        g = 1;
        b = -4 * percent + 2.5;
    } else if(0.625 <= percent && percent <= 0.875) {
        r = 1;
        g = -4 * percent + 3.5;
    } else if(0.875 <= percent && percent <= 1) {
        r = -4 * percent + 4.5;
    }
    else {
        r = g = b = 1;
    }
    r *= 255;
    g *= 255;
    b *= 255;
    return {
        red: r,
        green: g,
        blue: b,
    };
}

/**
 * Function used to determine which color to be used for text based on background color
 * 
 * @param {*} theColor
 *                  background color used to determine what color to be used for text
 * 
 * @returns a color to be used for text
 */
export function calculateTextColor(theColor) {
    const a = 1 - ((0.299 * theColor.red) + (0.587 * theColor.green) + (0.114 * theColor.blue))/255
    return a < 0.5 ? Colors.DARK : Colors.LIGHT
}

export function heatMapNoColor(value, min, max) {
    if (value === 0) {
        return {h:0, s:1, l:0}
    }
    return {h:0, s:1.0, l:1.0}
}

export function heatMapBlues(value, min, max) {
    if (value === 0) {
        return {h:0, s:1, l:0}
    } else if (min === max) {
        return {h:250 / 360, s:.5, l:.75}
    }
    const range = Math.abs(max - min)
    const norm = (value - min) / range
    let hsl = {h:0, s:.5, l:.75}
    hsl.h = ((Math.abs(norm) * 60) + 190) / 360
    return hsl
}

export function heatMapReds(value, min, max) {
    if (value === 0) {
        return {h:0, s:1, l:0}
    } else if (min === max) {
        return {h:1 / 360, s:.85, l:.7}
    }
    const range = Math.abs(max - min)
    const norm = (value - min) / range
    const scale = (25 - range / 2 * 25) < 0 ? 25 : (25 - range / 2 * 25)
    let hsl = {h:0, s:.85, l:.7}
    hsl.h = (Math.abs(1 - norm) * (35 + scale)) / 360
    return hsl
}

export function heatMapGreens(value, min, max) {
    if (value === 0) {
        return {h:0, s:1, l:0}
    } else if (min === max) {
        return {h:120 / 360, s:.75, l:.75}
    }
    const range = Math.abs(max - min)
    const norm = (value - min) / range
    let hsl = {h:0, s:.75, l:.75}
    hsl.h = ((Math.abs(norm) * 60) + 60) / 360
    return hsl
}

export function getComplement(hsl) {
    let r = {...hsl}
    if(tc.fromRatio(r).isLight()) {
        r.l = .05
    } else {
        r.l = .95
    }
    return r
}

export function hslToString(hsl) {
    return `hsl(${hsl.h * 360}, ${hsl.s * 100}%, ${hsl.l * 100}%)`
}

export function calculateGradient(hsl) {
    let a = tc.fromRatio(hsl).toHexString()
    // let b = tc.fromRatio(hsl).lighten().toHexString()
    // return  `linear-gradient(to right, ${a}, ${a} 10%, ${b} 10%, ${b} 90%, ${a} 90%)`
    let b = tc.fromRatio(hsl).lighten().lighten().lighten().toHexString()
    return  `radial-gradient(circle, ${b}, ${a} 70%)`
    // let b = tc.fromRatio(hsl).lighten().lighten().toHexString()
    // return  `repeating-linear-gradient(45deg, ${a}, ${a} 12px, ${b} 3px, ${b} 15px)`
}

export const heatmaps = [{name:"Reds", colorClass:"danger", func:heatMapReds}, 
                            {name:"Blues", colorClass:"primary", func:heatMapBlues},
                            {name:"Greens", colorClass:"success", func:heatMapGreens},  
                            {name:"No Color", colorClass:"secondary", func:heatMapNoColor}]